@import "_0-config.scss";
/*
    3. ADD THE AWESOME

    keywords: CTA in your face, focus dialog pattern

*/
body.step3 {
    .requestform {
        background-color: $color-background;
        box-sizing: border-box;
        height: 0;
        max-width: 1rem * 65 / 2;
        margin: 0;
        opacity: 0;
        overflow: hidden;
        padding: 1rem;
        position: fixed;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
        transition: 0.25s opacity ease-in;
        width: 100%;
        z-index: 7000;

        fieldset {
            border: 0;
            display: block;
            margin: 0;
            padding: 0;
            width: 100%;

            h2 {
                margin-top: 0;
            }
        }
    }

    .button[for="calltoaction"] {
        display: inline-block;
    }

    #calltoaction {
        cursor: pointer;
        display: block;
        margin: 0;
        opacity: 0;
        position: fixed;
        height: 0;
        left: 0rem;
        top: 0rem;
        transition: 0.25s opacity ease-in;
        z-index: 6000;

        &:checked {
            opacity: 1;
            width: 100%;

            &:before {
                color: $color-background;
                content: "\00d7";
                display: block;
                font-size: 2rem;
                height: 1rem;
                position: absolute;
                right: 1rem;
                width: 1rem;
                z-index: 8000;
            }

            &:after {
                background: rgba($color-neutral-dark,0.78);
                content: "";
                display: block;
                height: 100vh;
                left: 0;
                position: absolute;
                top: 0;
                width: 100vw;
            }

            + main {
                height: calc(100vh - 2rem);
                overflow: hidden;
            }

            + main .requestform {
                opacity: 1;
                height: auto;
            }
        }
    }
}