@import "_0-config.scss";

/*
    1. APPLY COATING

    keywords: colors, font sizes, spacing, line width, BEM, ALPBETICAL

*/

/*
    BEM = naming convention
    .block {}
    .block__element {}
    .block--modifier {}

     .person {}
    .person__hand {}
    .person--female {}
    .person--female__hand {}
    .person__hand--left {}
*/

html {
    font-size: 18px;
}

body.step1 {
    background-color: $color-background;
    font-family: $FontFamily;
    font-size: 1rem;
    font-weight: lighter;
    line-height: 1.5rem;
    overflow-y: scroll;
    margin:0;
}

body.step1 {

    h1, h2, h3, h4, h5 {
        margin: 0.5rem 0 0.5rem 0;
        font-weight: 300;
        line-height: 1.5em;
    }

    h2 {
        margin-top: 2rem;
    }

    p {
        margin: 0 0 0.5rem 0;
    }

    input,
    select,
    button,
    textarea {
        color: #222;
        padding: 0 0.2rem;
        border: 1px solid #b0b0b0;
        vertical-align: middle;
        white-space: normal;
        font-size: 1rem;
        font-family: $FontFamily;
        font-weight: lighter;

        &:focus {
            outline: 0;
        }
    }

    button {
        padding: 0.5rem;
        cursor: pointer;
    }

    nav {
        span {
            padding: 1rem;
        }
    }


    a {
        color: $color-accent;

        &:hover {
            color: darken($color-accent,10%)
        }
    }

    main {
        margin: auto;
        max-width: 1rem * 65 / 2;
        padding: 1rem 2rem;
    }

    .requestform {
        fieldset {
            border-color: $color-neutral-light;
            margin-top: 2rem;

            h2 {
                margin-top: 0;
            }
        }

        label {
            display: inline-block;
            width: 20%;
        }

        textarea {
            box-sizing: border-box;
            width: 100%;
            height: 10rem;
        }

        input {
            width: 77%;
        }
    }
}
