.button[for="calltoaction"] {
  display: none;
}

.question__selector {
  display: none;
}

#calltoaction {
  display: none;
}

.accordian__item {
  display: none;
}

* {
  transition: all 0.25s;
}

/*
    BAKING THE MAGICAL UNICORN
    
    keywords: gulp, sass-config (color palette), to framework or not?, 
    autoprefixer, sourcemaps
*/

/*
    1. APPLY COATING

    keywords: colors, font sizes, spacing, line width, BEM, ALPBETICAL

*/

/*
    BEM = naming convention
    .block {}
    .block__element {}
    .block--modifier {}

     .person {}
    .person__hand {}
    .person--female {}
    .person--female__hand {}
    .person__hand--left {}
*/

html {
  font-size: 18px;
}

body.step1 {
  font: lighter 1rem/1.5rem "Segoe UI", sans-seriif;
  background: #f2f2f2;
  overflow-y: scroll;
  margin: 0;
}

body.step1 h1,
body.step1 h2,
body.step1 h3,
body.step1 h4,
body.step1 h5 {
  margin: 0.5rem 0 0.5rem 0;
  font-weight: 300;
  line-height: 1.5em;
}

body.step1 h2 {
  margin-top: 2rem;
}

body.step1 p {
  margin: 0 0 0.5rem 0;
}

body.step1 input,
body.step1 select,
body.step1 button,
body.step1 textarea {
  font: lighter 1rem "Segoe UI", sans-seriif;
  color: #222;
  padding: 0 0.2rem;
  border: 1px solid #b0b0b0;
  vertical-align: middle;
  white-space: normal;
}

body.step1 input:focus,
body.step1 select:focus,
body.step1 button:focus,
body.step1 textarea:focus {
  outline: 0;
}

body.step1 button {
  padding: 0.5rem;
  cursor: pointer;
}

body.step1 nav span {
  padding: 1rem;
}

body.step1 a {
  color: #5AAF41;
}

body.step1 a:hover {
  color: #478a33;
}

body.step1 main {
  margin: auto;
  max-width: 32.5rem;
  padding: 1rem 2rem;
}

body.step1 .requestform fieldset {
  border-color: #f2f2f2;
  margin-top: 2rem;
}

body.step1 .requestform fieldset h2 {
  margin-top: 0;
}

body.step1 .requestform label {
  display: inline-block;
  width: 20%;
}

body.step1 .requestform textarea {
  box-sizing: border-box;
  width: 100%;
  height: 10rem;
}

body.step1 .requestform input {
  width: 77%;
}

/*
    2. ADD THE COOL

    keywords: focus on content and CTA, advanced ui elements, simple transitions,


*/

body.step2 a {
  display: inline-block;
  position: relative;
  text-decoration: none;
}

body.step2 a:after,
body.step2 a:before {
  border-bottom: 1px solid #478a33;
  bottom: 0;
  content: "";
  display: inline-block;
  height: 2px;
  left: 0;
  position: absolute;
  transition: all 0.15s ease-out;
  width: 0;
}

body.step2 a:before {
  border-bottom-color: #5AAF41;
  border-bottom-style: dashed;
  width: 100%;
}

body.step2 a:hover:after {
  width: 100%;
}

body.step2 .button {
  background: #5AAF41;
  border: 0;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  padding: 0.5rem;
}

body.step2 .button:hover {
  background: #478a33;
}

body.step2 form h2 {
  margin-top: 0;
}

body.step2 .accordian__item {
  display: none;
}

body.step2 .accordian__item + h2 label {
  cursor: pointer;
}

body.step2 .accordian__item + h2 label:before {
  content: "\005e";
  color: #5AAF41;
  display: inline-block;
  margin-right: 0.5rem;
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
  transition: all 0.25s;
}

body.step2 .accordian__item + h2 + p {
  border-bottom: 1px solid #333;
  display: block;
  height: 100%;
  padding-bottom: 0rem;
  max-height: 0;
  overflow: hidden;
  opacity: 0;
  transition: all 0.25s;
}

body.step2 .accordian__item:checked + h2 + p {
  border-bottom-color: #5AAF41;
  max-height: 500px;
  opacity: 1;
  padding-bottom: 2rem;
}

body.step2 .accordian__item:checked + h2 label:before {
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
}

/*
    3. ADD THE AWESOME

    keywords: CTA in your face, focus dialog pattern

*/

body.step3 .requestform {
  background: #f2f2f2;
  box-sizing: border-box;
  height: 0;
  max-width: 32.5rem;
  margin: 0;
  opacity: 0;
  overflow: hidden;
  padding: 1rem;
  position: fixed;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  transition: 0.25s opacity ease-in;
  width: 100%;
  z-index: 7000;
}

body.step3 .requestform fieldset {
  border: 0;
  display: block;
  margin: 0;
  padding: 0;
  width: 100%;
}

body.step3 .requestform fieldset h2 {
  margin-top: 0;
}

body.step3 .button[for="calltoaction"] {
  display: inline-block;
}

body.step3 #calltoaction {
  cursor: pointer;
  display: block;
  margin: 0;
  opacity: 0;
  position: fixed;
  height: 0;
  left: 0rem;
  top: 0rem;
  transition: 0.25s opacity ease-in;
  z-index: 6000;
}

body.step3 #calltoaction:checked {
  opacity: 1;
  width: 100%;
}

body.step3 #calltoaction:checked:before {
  color: #f2f2f2;
  content: "\00d7";
  display: block;
  font-size: 2rem;
  height: 1rem;
  position: absolute;
  right: 1rem;
  width: 1rem;
  z-index: 8000;
}

body.step3 #calltoaction:checked:after {
  background: rgba(51, 51, 51, 0.78);
  content: "";
  display: block;
  height: 100vh;
  left: 0;
  position: absolute;
  top: 0;
  width: 100vw;
}

body.step3 #calltoaction:checked + main {
  height: calc(100vh - 2rem);
  overflow: hidden;
}

body.step3 #calltoaction:checked + main .requestform {
  opacity: 1;
  height: auto;
}

/*
    4. SPRINKLE SOME MAGIC

    keywords: conversational UI

*/

body.step4 .requestform {
  background: transparent;
  max-width: 80%;
}

body.step4 .requestform h2,
body.step4 .requestform p {
  display: none;
}

body.step4 .requestform input {
  width: auto;
}

body.step4 .requestform input[type='datetime-local'],
body.step4 .requestform input[type='number'] {
  width: 100%;
}

body.step4 .questions {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 0;
  -ms-flex: 0 1 auto;
  flex: 0 1 auto;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -ms-flex-direction: row;
  flex-direction: row;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}

body.step4 .question {
  max-height: 0;
  max-width: 0;
  opacity: .3;
  overflow: hidden;
  transition: opacity 0.25s;
  width: 100%;
}

body.step4 .question label {
  color: #fff;
  display: block;
  font-size: 0;
  line-height: 0;
  width: 100%;
}

body.step4 .question label:after {
  content: attr(data-moreInformation);
  display: block;
  font-size: 3rem;
  font-weight: 500;
  line-height: 1em;
  width: 100%;
}

body.step4 .question__selector {
  cursor: pointer;
  display: inline !important;
  height: 2rem;
  margin-top: 1rem;
  -webkit-box-ordinal-group: 101;
  -ms-flex-order: 100;
  order: 100;
  width: 2rem;
}

body.step4 .question__selector:checked {
  height: 0;
  width: 0;
  margin: 0;
}

body.step4 .question__selector:checked:last-of-type {
  display: none;
}

body.step4 .question__selector:checked ~ .question__selector {
  opacity: 0;
  margin: 0;
}

body.step4 .question__selector:checked + .question + .question__selector {
  opacity: 1;
  height: 0;
  background: red;
}

body.step4 .question__selector:checked + .question + .question__selector:before {
  background: #5AAF41;
  content: "volgende vraag";
  color: #fff;
  display: inline-block;
  line-height: 1;
  margin-top: 1rem;
  position: absolute;
  padding: .5rem;
  right: 1rem;
  white-space: nowrap;
  z-index: 9000;
}

body.step4 .question__answer {
  width: 100%;
}

body.step4 .question__answer--text {
  color: #fff;
}

body.step4 .question__answer--text div {
  margin-bottom: 1rem;
  font-size: 2rem;
  line-height: 1.2em;
}

body.step4 .question__send {
  margin-top: .8rem;
  position: absolute;
  right: 1rem;
}

body.step4 .question__selector:checked + .question {
  opacity: 1;
  max-height: 100%;
  max-width: 100%;
}

body.step4 .question__selector:checked + .question select.question__answer {
  margin-top: 2rem;
}

body.step4 select.question__answer option {
  height: 1rem;
  color: #333;
}

body.step4 textarea.question__answer,
body.step4 select.question__answer,
body.step4 input.question__answer {
  background: transparent;
  border: 0;
  border-bottom: 1px solid #fff;
  color: #fff;
  font-size: 2rem;
}

/*
   ADD SOME DEMO goodness
*/

nav.recipe {
  position: fixed;
  padding: 1rem;
  top: 0;
  right: 2rem;
  background: #5AAF41;
}

nav.recipe:before {
  content: "Demo recipe ";
  color: #fff;
}

nav.recipe:after {
  content: "demo works only on chrome";
  display: block;
  font-style: italic;
  color: #fff;
  font-size: .85rem;
}

nav.recipe input {
  cursor: pointer;
  padding: 1rem;
}